import React, { useState } from "react";
import { FormInstance, Form, Input, InputNumber, Button } from "antd";
import { Select, Tooltip } from "antd";
import { useNavigate } from "react-router-dom";
import { CaretDownOutlined } from "@ant-design/icons";
import { positionList, riskType, generalDirector } from "./const";
import "./styles.css";

const { Option } = Select;

const RiscForm = ({ form, initialValues = {} }) => {
  const [position, setPosition] = useState("");
  const navigate = useNavigate();

  const onFinish = (data) => {
    if (data.position !== generalDirector) {
      console.log({ ...data, riskType: riskType[data.position] });
      navigate("/overview");
    } else {
      console.log(data);
      navigate("/overview");
    }
  };
  const positionFields = {
    " ": () => {
      return (
        <Form.Item
          className="field"
          name="riskType"
          label=" "
          rules={[{ required: true, message: " '" }]}
        >
          <Input type="text" placeholder=" " />
        </Form.Item>
      );
    },
    "  ": () => {
      return (
        <Form.Item className="field" name="riskType" label=" ">
           
        </Form.Item>
      );
    },
    "  ": () => {
      return (
        <Form.Item className="field" name="riskType" label=" ">
           
        </Form.Item>
      );
    },
    "  ": () => {
      return (
        <Form.Item className="field" name="riskType" label=" ">
           
        </Form.Item>
      );
    },
    "  ": () => {
      return (
        <Form.Item className="field" name="riskType" label=" ">
           
        </Form.Item>
      );
    },
  };
  const handleChange = (val) => {
    setPosition(val);
  };
  return (
    <Form
      layout="vertical"
      name="new"
      form={form}
      onFinish={onFinish}
      className="riskForm"
    >
      <Form.Item
        label=""
        name="position"
        rules={[{ required: true, message: "  '!" }]}
      >
        <Select
          onChange={() => {}}
          suffixIcon={<CaretDownOutlined />}
          onChange={handleChange}
        >
          {positionList.map((optionValue) => (
            <Option key={optionValue} value={optionValue}>
              {optionValue}
            </Option>
          ))}
        </Select>
      </Form.Item>
      {position && (
        <div className="riskWrapper">
          {positionFields[position]()}
          <Form.Item
            className="field"
            name="degreeOfRisk"
            label="  (   1-10)"
            rules={[{ required: true, message: " '" }]}
          >
            <InputNumber min={0} max={10} placeholder=" " />
          </Form.Item>
          <Form.Item
            className="field"
            label="  '"
            name="fullName"
            rules={[{ required: true, message: " '" }]}
          >
            <Input type="text" placeholder="  '" />
          </Form.Item>
          <Form.Item
            className="field"
            label=" "
            name="projectName"
            rules={[{ required: true, message: " '" }]}
          >
            <Input type="text" placeholder=" " />
          </Form.Item>
        </div>
      )}
      <Form.Item>
        <Button type="primary" htmlType="submit" disabled={!position}>
          Sumbit
        </Button>
      </Form.Item>
    </Form>
  );
};

export default RiscForm;
